<template>
  <div class="screen">
      <ul class="imageWrapper">
          <li class="questionImage question1">
              <div class="questionImageText">
                  <span class="index one">1</span>
                  <span class="choice">{{answer1}}</span>
                  <span class="selected">18</span>
              </div>
          </li>
          <li class="questionImage question2">
              <div class="questionImageText">
                  <span class="index two">2</span>
                  <span class="choice">{{answer2}}</span>
                  <span class="selected">18</span>
              </div>
          </li>
          <li class="questionImage question3">
              <div class="questionImageText">
                  <span class="index three">3</span>
                  <span class="choice">{{answer3}}</span>
                  <span class="selected">18</span>
              </div>
          </li>
          <li class="questionImage question4">
              <div class="questionImageText">
                  <span class="index four">4</span>
                  <span class="choice">{{answer4}}</span>
                  <span class="selected">18</span>
              </div>
          </li>
      </ul>
      <div class="questionText">
          <div class="questionLogo">Q</div>
          <div class="questionInnerText">
              <span>{{problemText}}</span>
          </div>
          <div class="time">
              <span>{{timer}}</span>
          </div>
      </div>
      <div class="timeupText">
          TIME UP
      </div>
  </div>
</template>

<script>
    import * as $ from 'jquery';
    import url from '../assets/url.js';
    export default {
        data () {
            return {
                timer: 10,
                text: '選択肢',
                startCountDown: '',
                answer: 1,
                url: 'http://' + window.url,
                problemText: '',
                answer1: '',
                answer2: '',
                answer3: '',
                answer4: ''
            }
        },
        methods: {
            countDown: function(){
                this.timer -= 1;
                if(this.timer === 0){
                    // alert('over');
                    $('.screen').addClass('done');
                    $('.timeupText').css('display', 'block');
                    clearInterval(this.startCountDown);
                }
                return false;
            }
        },
        mounted () {
            // console.log($(window).height());
            $('.imageWrapper').css('display', 'none');
            var self = this;

            // $.ajax(self.url + '/steps/getProblem?user_id=' + 3, {
            $.ajax(self.url + '/rounds/getRoundProblem', {
                method: 'POST',
                type: 'POST',
                cache: false
            })
            .done(function(json){
                console.log(json);
                var data = json;
                self.problemText = data.problem.problem_text;
                $.each(data.answers, function(i){
                  // 選択肢のパッケージング
                  var answer = data.answers[i].answer_text
                  if(i === 0){
                    self.answer1 = answer;
                  }
                  else if(i === 1){
                    self.answer2 = answer;
                  }
                  else if(i === 2){
                    self.answer3 = answer;
                  }
                  else if(i === 4){
                    self.answer4 = answer;
                  }

                  if(data.answers[i].answer_flg === true){
                    self.answer = i + 1;
                    console.log('答えは ' + self.answer + 'です');
                  }

                })

            });

            var self = this;
            if(this.state === 0){
                $('.count:eq(0)').css('background-color', '#8540bf');
                $('.count:eq(1)').css('background-color', '#fd0000');
                $('.count:eq(2)').css('background-color', '#00ff00');
                $('.count:eq(3)').css('background-color', '#f19149');
            }

            $(document).on('keyup', function(e){
                console.log(e.keyCode);
                var key = e.keyCode;

                // Sキー
                // 問題一覧ページ（スクリーン）
                if(key === 83){
                    // location.href="#screen";
                    // 回答状況を取得するAPIを叩く…？
                    $.ajax(self.url + '/steps/ranking',{
                        method:'POST',
                        type:'POST',
                        cache:false
                    })
                    .done(function(json){
                        console.log(json);
                        $('.selected').css('display', 'block');
                    })
                    .fail(function(err){});
                }
                // Rキー
                else if(key === 82){
                  location.href="#ranking";
                }
                // Lキー
                else if(key === 76){
                    location.href="/";
                    return false;
                }
                // Tキー
                // カウントダウンスタート
                else if(key === 84){
                    self.startCountDown = setInterval(self.countDown, 1000);
                }
                // Yキー
                // DONEクラスremove
                else if(key === 89){
                    $('.screen').removeClass('done');
                    $('.timeupText').css('display', 'none');
                }

                // Aキー
                // 答えオープン
                else if(key === 65){
                  console.log(self.answer);
                    if(self.answer === 1){
                        $('.question2').addClass('done');
                        $('.question3').addClass('done');
                        $('.question4').addClass('done');
                    }
                    else if(self.answer === 2){
                        $('.question1').addClass('done');
                        $('.question3').addClass('done');
                        $('.question4').addClass('done');
                    }
                    else if(self.answer === 3){
                        $('.question1').addClass('done');
                        $('.question2').addClass('done');
                        $('.question4').addClass('done');
                    }
                    else if(self.answer === 4){
                        $('.question2').addClass('done');
                        $('.question3').addClass('done');
                        $('.question1').addClass('done');
                    }
                }

                // Oキー
                // 問題表示
                else if(key === 79){
                    $('.imageWrapper').css('display', 'block');
                }

                else {
                    return false;
                }

                // 答えた人数公開ボタンが必要！

                return false;
            });
        }

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .screen{
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        font-size: 100%;
        width: 100%;
        height: 100%;
        background: url('../assets/screen_bg.png') no-repeat;
        background-size: cover;
        overflow: hidden;
    }

    .done {
        background-color: #eeeeee;
        opacity: 0.6;
        z-index: -3;
    }

    .imageWrapper {
        position: absolute;
        top: 15%;
        left: 10%;
        width: 70%;
        height: 80%;
        -moz-column-count: 2;
        -webkit-column-count: 2;
        column-count: 2;
        -moz-column-gap: 1em;
        -webkit-column-gap: 1em;
        column-gap: 1em;

        .questionImage {
            // height: 40%;
            height: 43%;
            background-size: cover;
            background-image: url('../assets/question_sample.jpg');
            margin-right: 5%;
            margin-bottom: 10%;
            border-radius: 0.8em;
            border: solid 10px #d9e2f4;
            position: relative;

            .questionImageText {
                position: absolute;
                bottom: 0;
                z-index: 3;
                height: 20%;
                width: 100%;
                text-align: center;
                background: #000;
                color: #fff;
                display: flex;
                justify-content: center;
                align-items: center;

                .index {
                    font-size: 2.0rem;
                    position: absolute;
                    left: 0;
                    // line-height: 60px;
                    padding: 10px 20px 10px 20px;
                    margin-left: 20px;
                    border-radius: 50%;
                    background-color: red;
                    height: 40px;

                }
                .choice {
                    font-size: 3.0rem;
                    font-weight: bold;
                    line-height: 60px;

                }

                .selected {
                    position: absolute;
                    right: 0;
                    font-size: 2.0rem;
                    line-height: 60px;
                    background-color: #77c2ca;
                    margin-right: 5px;
                    // padding-left: 50px;
                    padding-left: 5%;
                    padding-right: 10px;
                    border-radius: 0.5em;
                    font-weight: bold;
                    color: #090a41;
                    display: none;
                }

                .one {
                    background-color: #8540bf;
                }

                .two {
                    background-color: #fd0000;
                }

                .three {
                    background-color: #00ff00;
                }

                .four {
                    background-color: #f19149;
                }
            }
        }
    }

    .questionText {
        position: absolute;
        top: 13%;
        left: 83%;
        width: 200px;
        // min-height: 500px;
        height: 70%;
        background: -webkit-linear-gradient(top, #1e5799 0%,#302689 1%,#302689 50%,#2989d8 80%,#2353e6 100%);
        opacity: 0.9;
        font-family: "ヒラギノ丸ゴ Pro W4","Hiragino Maru Gothic Pro", 'Avenir', Helvetica, Arial, sans-serif;
        border-radius: 0.8em;

        .questionLogo {
            font-size: 6.0rem;
            text-align: center;
            color: #39fed5;
            font-weight: bold;
            opacity: 1.0;
        }

        .questionInnerText{
            // position: relative;
            // // top: 10%;
            position: absolute;
            // top: 15%;
            top: 20%;
            left: -33%;
            background-color: #b6c5e0;
            color: #160087;
            font-weight: bold;
            margin: 5%;
            margin-left: auto;
            margin-right: auto;
            min-width: 90px;
            width: 40%;
            min-height: 100px;
            height: 65%;
            padding: 5% 20%;
            border-radius: 0.8em;
            -webkit-writing-mode: vertical-rl;
            -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
            font-size: 2.5rem;
            text-align: center;
            opacity: 1.0;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .time {
            position: absolute;
            top: 90%;
            // left: -10%;
            left: -5%;
            text-align: center;
            z-index: 2;
            font-size: 3.0rem;
            color: #f0f0f0;
            font-weight: bold;
            border: 10px solid #cdd2de;
            background-color: #940100;
            // max-width: 150px;
            // max-height: 150px;
            width: 200px;
            height: 200px;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            margin-left: auto;
            margin-right: auto;
            opacity: 1.0;
            display: flex;
            justify-content: center;
            align-items: center;

            span {
                // font-size: 5.0rem;
                font-size: 80px;
                // position: absolute;
                // top: 20%;
                // left: 25%;
            }
        }
    }

    .timeupText {
        display: none;
        position: absolute;
        top: 50%;
        left: 30%;
        font-size: 5.0rem;
        color: red;
        text-shadow:4px 2px 5px #000000;
        opacity: 1.0;
        z-index: 4;
    }

    @media(max-height: 800px) {
        .imageWrapper {
            left: 5%;

            .questionImageText {
                .choice {
                    font-size: 1.0rem;
                }
            }
        }
        .questionText {
            top: 4%;
            left: 77%;

            .questionInnerText {
                font-size: 1.0rem;
                height: 55%;
                top: 25%;
            }
        }
    }
</style>



